@import "~react-image-gallery/styles/css/image-gallery.css";
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@font-face {
	font-family: 'SegoeUI';
	src: local('SegoeUI'), url(../assets/fonts/segoe-ui.otf) format('otf');
}
@font-face {
	font-family: 'Helvetica';
	src: local('Helvetica'), url(../assets/fonts/helvetica.ttf) format('ttf');
}
@font-face {
	font-family: 'HelveticaNeue';
	src: local('HelveticaNeue') url(../assets/fonts/helvetica-neue.ttf) format('ttf');
}

body {
	width: 100% !important;
	max-width: 100% !important;
}

#root {
	width: 100% !important;
	overflow-x: hidden !important;
}
h4 {
	font-family: 'SegoeUI' !important;
	color: #FFF !important ;
	font-weight: 700 !important;
	font-size: 0.9rem !important
}

/* contato */
.contato-descricao span {
	color: #fff !important;
	font-weight: normal;
	font-family: 'SegoeUI' !important;
	font-size: 0.8rem !important
}
.contato-tels{
	display: flex;
	justify-content: flex-start;
	width: 100%;
	font-family: 'SegoeUI' !important;
	font-size: 1.2rem;
	font-weight: 700;
	color: #FFF;
	margin-top: 40px;
}
.contato-tels-icone img{
	width: 2rem;
	height: 2rem;
}
.contato-tels-numeros{
	margin-left: 2%;
	display: flex;
	align-items: center;
	width: 100%;
}
.contato-tels-numeros span:nth-child(2), .contato-tels-numeros span:nth-child(3){
	border-left: 2px solid #FFF;
	margin-left: 1%;
	padding-left: 1%;
}
@media (max-width: 800px){
	.contato-tels{
		flex-direction: column;
		text-align: center;
		font-size: 1.5rem;
	}
	.contato-tels-numeros{
		flex-direction: column;
		margin-left: 0;
		margin-top: 10px;
	}
	.contato-tels-numeros span:nth-child(2), .contato-tels-numeros span:nth-child(3){
		border-left: none;
		margin-left: 0;
		padding-left: 0;
	}
}

.btn-contato{
	margin: 12px
}

/* MENU */
.rs-nav-horizontal {
	display: flex !important;
	justify-content: center !important;
	height: 40px !important;
}

/* .rs-nav-horizontal ul {
	width: 80% !important;
    display: flex !important;
	justify-content: space-between !important;
	align-items: flex-end !important;
} */

.rs-nav-horizontal ul li a::before{
	background-color: #0099FF !important;
}
.rs-nav-horizontal ul li a {
	/* color: #004564 !important; */
	font-weight: 600 !important;
	font-family: 'SegoeUI' !important;
	font-size: 1rem !important
}

.rs-nav-horizontal ul .rs-nav-item-active a {
	color: #0099FF !important;
}

.rs-nav-horizontal ul li:hover a {
	color: rgba(0,73,183,1) !important;
	text-decoration: dotted !important;
}

/* Carrossel */

.atendimento-depoimentos{
	background-color: #FFF8F9;
	padding:50px;
	display: flex;
	flex-flow: column wrap;
	align-items: center;
	justify-content: center;
	height:100%;
  }

.homeCarrossel {
	height: 500px !important;
	margin-top: 100px !important
}
.empresaCarrossel {
	height: 450px !important;
	margin-top: 0px !important;
	/* padding-bottom: 80px !important */

}
.empresaCarrossel .awssld__content {
	background: #f7f7f7 !important;

}

.empresaCarrossel .awssld__bullets {
	bottom: 70px !important;
	z-index: 9999 !important;
}

.empresaCarrossel .awssld__bullets button:not(.awssld__bullets--active) {
	background: #FFF !important;
	border: 1px solid rgb(0,73,183) !important
}

.empresaCarrossel .awssld__bullets--active {
	background: rgb(0,73,183) !important;
	background: linear-gradient(20deg, rgba(0,73,183,1) 35%, rgba(0,153,255,1) 100%) !important;
}
.slider_1 {
	background: url(../assets/images/slide_1.png) !important;
	background-size: cover !important;
    background-position: center center !important;
    background-repeat: no-repeat !important;
	width: 100% !important;
	height: 100% !important;
	display: flex !important;
	justify-content: flex-start !important;
	align-items: center !important;
}
.slider_2 {
	background: url(../assets/images/slide_2.png) !important;
	background-size: cover !important;
    background-position: center center !important;
    background-repeat: no-repeat !important;
	width: 100% !important;
	height: 100% !important;
	display: flex !important;
	justify-content: flex-start !important;
	align-items: center !important;
}
.slider_3 {
	background: url(../assets/images/slide_3.png) !important;
	background-size: cover !important;
    background-position: center center !important;
    background-repeat: no-repeat !important;
	width: 100% !important;
	height: 100% !important;
	display: flex !important;
	justify-content: flex-start !important;
	align-items: center !important;
}

.slider_1 div, .slider_2 div, .slider_3 div {
	padding: 15% !important
}

.slider_1 div h2, .slider_2 div h2, .slider_3 div h2 {
	font-family: 'Helvetica' !important;
	color: #FFF !important ;
	font-weight: bold !important;
	font-size: 3rem !important;
	line-height: 3.5rem;
}

.slider_1 div p, .slider_2 div p, .slider_3 div p {
	margin-top: 20px;
	font-family: 'Roboto', sans-serif !important;
	color: #FFF !important ;
	font-weight: normal !important;
	font-size: 1.2rem !important
}

.slider_1 div .btn-saiba-mais, .slider_2 div .btn-saiba-mais, .slider_3 div .btn-saiba-mais, .btn-enviar {
	margin-top: 40px !important;
	/* width: 20%; */
	/* height: 50px !important; */
	background: rgb(0,73,183);
	background: linear-gradient(90deg, rgba(0,73,183,1) 35%, rgba(0,153,255,1) 100%) !important;
	font-family: 'Roboto', sans-serif !important;
	color: #FFF !important ;
	font-weight: 700 !important;
	font-size: 1rem !important;
}

.card-porta-titulo{
	font-family: 'SegoeUI' !important;
	font-size: 50;
	font-weight: normal;
	color: #000000
}

.card-porta-descricao{
	font-family: 'Helvetica' !important;
	font-size: 20px;
	font-weight: normal;
	color: #3E3E3E
}

.homeCarrossel .awssld__bullets {
	bottom: 10px !important
}


.homeCarrossel .awssld__bullets button:not(.awssld__bullets--active) {
	background: #FFF !important;
}

.homeCarrossel .awssld__bullets--active {
	background: rgb(0,73,183) !important;
	background: linear-gradient(20deg, rgba(0,73,183,1) 35%, rgba(0,153,255,1) 100%) !important;
}

.parallax {
	background: url(../assets/images/background_servicos.jpg) !important;
	background-size: cover !important;
    background-position: center center !important;
    background-repeat: no-repeat !important;
}
